import { ICompany } from "entities/company/types";
import { api } from "shared/config/api";

export const API_COMPANIES = {
  async getAll(user_id: string) {
    const { data: response } = await api.get<ICompany[]>(
      "/companies/all/" + user_id
    );
    return response;
  },
  async add(data: ICompany, user_id: string) {
    const { data: response } = await api.post<ICompany>("/companies", {
      ...data,
      user_id,
    });
    return response;
  },
};
